.full-page {
  position: relative;
  z-index: 2;
  // overflow-y: hidden;

  .js.js-loaded & {
    display: block;
  }

  &__section {
    position: relative;
    z-index: 10;

    @media (min-width: @screen-xl) {
      max-width: 1200px;
      margin: auto;
    }

    @media (min-width: @screen-xxl) {
      max-width: 1920px;
    }

    &--bordered {

      @media (max-width: (@screen-xl - 1)) {
        margin-bottom: 40px;
      }

      @media (min-width: @screen-xl) {
        margin-top: 40px;

        &:before {
          content: '';
          position: absolute;
          z-index: -1;
          top: 70px;
          right: 155px;
          bottom: 52px;
          left: 95px;
          border: 1px solid @gray-lighter;
          opacity: 0;

          .js-loaded & {
            animation: opacity-none 0.3s 1.2s forwards;
          }
        }
      }

      @media (min-width: @screen-xxl) {
        margin-top: 90px;

        &:before {
          top: 60px;
          right: 355px;
          bottom: 52px;
          left: 164px;
          border: 1px solid @gray-lighter;
        }
      }
    }
  }

  &__section-inner {

    @media (max-width: (@screen-xl - 1)) {
      margin-left: 10px;
      margin-right: 10px;
    }

    @media (min-width: @screen-xl) {

      .main-header:not(.main-header--single-page) ~ & {
        margin-bottom: 120px;
      }
    }

    @media (min-width: @screen-xxl) {

      .main-header:not(.main-header--single-page) ~ & {
        margin-bottom: 205px;
      }
    }
  }

  &__video-wrap {
    display: block;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: hidden;

    .vsc-controller {
      display: none !important;
    }

    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: @color-main;
      opacity: 0.85;
    }
  }

  &__video {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;

    @media (min-width: @screen-xl) {
      display: block;
    }
  }

  &__video-poster {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;

    @media (min-width: @screen-xl) {
      display: none;
    }
  }

  &__pseudovideo {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__counter {
    display: block;
    font-size: 20px;
    text-align: right;
    letter-spacing: 0.1em;
    color: @gray-darkest;
  }

  &__scroll-me-baby {
    width: 180px;
    position: absolute;
    top: 100%;
    left: 42px;
    font-size: 16px;
    color: @gray-light;
    letter-spacing: 0.05em;
    border-bottom: 3px solid @gray-lighter;
    transform-origin: 0 100%;
    transform: rotate(90deg);

    @media (min-width: @screen-xxl) {
      left: 90px;
    }

    &:before {
      content: '';
      position: absolute;
      width: 19px;
      height: 31px;
      right: -44px;
      top: 12px;
      transform: rotate(-90deg);
      background-image: data-uri('image/svg+xml;charset=UTF-8', './src/blocks/full-page/bg/mouse.svg');
      background-size: 100%;
      background-repeat: no-repeat;
    }
  }

  &__to-all-article-wrapper {
    text-align: center;

    @media (min-width: @screen-xl) {
      width: 850px;
    }

    @media (min-width: @screen-xxl) {
      width: 1118px;
    }
  }

  &__to-all-article {
    display: inline-block;
    padding: 30px;
    background-color: #fff;
  }





  &--home {
    background-color: @color-main;
    color: #fff;
  }

  &--content-page {

    @media (max-width: (@screen-xl - 1)) {
      // padding-top: 55px; //      
      // padding-bottom: 44px; //     
      transform: none !important; //     fullPages

      .fp-section,
      .fp-tableCell {
        display: block !important;
        height: auto !important;
      }
    }

    @media (min-width: @screen-xl) {

      .full-page__section {
        // padding-top: 80px;
        padding-left: 158px;
      }

      .full-page__section-inner {
        opacity: 0;

        .js-loaded & {
          animation: opacity-none 0.3s 1.2s forwards;
        }
      }

      .fp-tableCell {
        padding-top: 80px;
      }
    }

    @media (min-width: @screen-xxl) {

      .full-page__section {
        padding-left: 362px;
      }
    }
  }

  &--scroll-normal {

    @media (min-width: @screen-xl) {

      .full-page__section {
        padding-top: 80px;
        // height: 100vh;
        // overflow-y: auto;
      }
    }
  }
}



html.js-loaded,
.js-loaded body {
    margin: 0;
    padding: 0;

    /*Avoid flicker on slides transitions for mobile phones #336 */
    -webkit-tap-highlight-color: rgba(0,0,0,0);
}
#superContainer {
    height: 100%;
    position: relative;

    /* Touch detection for Windows 8 */
    -ms-touch-action: none;

    /* IE 11 on Windows Phone 8.1*/
    touch-action: none;
}
.fp-section {
    position: relative;
    -webkit-box-sizing: border-box; /* Safari<=5 Android<=3 */
    -moz-box-sizing: border-box; /* <=28 */
    box-sizing: border-box;
}
.fp-slide {
    float: left;
}
.fp-slide, .fp-slidesContainer {
    height: 100%;
    display: block;
}
.fp-slides {
    z-index:1;
    height: 100%;
    overflow: hidden;
    position: relative;
    -webkit-transition: all 0.3s ease-out; /* Safari<=6 Android<=4.3 */
    transition: all 0.3s ease-out;
}
.fp-section.fp-table, .fp-slide.fp-table {
    // display: table;
    // table-layout:fixed;
    // width: 100%;
}
.fp-tableCell {
    // display: table-cell;
    // vertical-align: middle;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    align-content: center;
    width: 100%;
    height: 100%;
}
.fp-slidesContainer {
    float: left;
    position: relative;
}
.fp-controlArrow {
    -webkit-user-select: none; /* webkit (safari, chrome) browsers */
    -moz-user-select: none; /* mozilla browsers */
    -khtml-user-select: none; /* webkit (konqueror) browsers */
    -ms-user-select: none; /* IE10+ */
    position: absolute;
    z-index: 4;
    top: 50%;
    cursor: pointer;
    width: 0;
    height: 0;
    border-style: solid;
    margin-top: -38px;
    -webkit-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
}
.fp-controlArrow.fp-prev {
    left: 15px;
    width: 0;
    border-width: 38.5px 34px 38.5px 0;
    border-color: transparent #fff transparent transparent;
}
.fp-controlArrow.fp-next {
    right: 15px;
    border-width: 38.5px 0 38.5px 34px;
    border-color: transparent transparent transparent #fff;
}
.fp-scrollable {
    overflow: hidden;
    position: relative;
}
.fp-scroller{
    overflow: hidden;
}
.iScrollIndicator{
    border: 0 !important;
}
.fp-notransition {
    -webkit-transition: none !important;
    transition: none !important;
}
#fp-nav {
    position: fixed;
    z-index: 6;
    margin-top: -32px;
    top: 50%;
    opacity: 1;
    -webkit-transform: translate3d(0,0,0);
}
#fp-nav.right {
    right: 17px;
}
#fp-nav.left {
    left: 17px;
}
.fp-slidesNav{
    position: absolute;
    z-index: 4;
    left: 50%;
    opacity: 1;
    -webkit-transform: translate3d(0,0,0);
    -ms-transform: translate3d(0,0,0);
    transform: translate3d(0,0,0);
}
.fp-slidesNav.bottom {
    bottom: 17px;
}
.fp-slidesNav.top {
    top: 17px;
}
#fp-nav ul,
.fp-slidesNav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
#fp-nav ul li,
.fp-slidesNav ul li {
    display: block;
    width: 14px;
    height: 13px;
    margin: 7px;
    position:relative;
}
.fp-slidesNav ul li {
    display: inline-block;
}
#fp-nav ul li a,
.fp-slidesNav ul li a {
    display: block;
    position: relative;
    z-index: 1;
    width: 100%;
    height: 100%;
    cursor: pointer;
    text-decoration: none;
}
#fp-nav ul li a.active span,
.fp-slidesNav ul li a.active span,
#fp-nav ul li:hover a.active span,
.fp-slidesNav ul li:hover a.active span{
    height: 12px;
    width: 12px;
    margin: -6px 0 0 -6px;
    border-radius: 100%;
 }
#fp-nav ul li a span,
.fp-slidesNav ul li a span {
    border-radius: 50%;
    position: absolute;
    z-index: 1;
    height: 4px;
    width: 4px;
    border: 0;
    background: #333;
    left: 50%;
    top: 50%;
    margin: -2px 0 0 -2px;
    transition: all 0.2s ease-in-out;
}
#fp-nav ul li:hover a span,
.fp-slidesNav ul li:hover a span{
    width: 10px;
    height: 10px;
    margin: -5px 0px 0px -5px;
}
#fp-nav ul li .fp-tooltip {
    position: absolute;
    top: -2px;
    color: #fff;
    font-size: 14px;
    font-family: arial, helvetica, sans-serif;
    white-space: nowrap;
    max-width: 220px;
    overflow: hidden;
    display: block;
    opacity: 0;
    width: 0;
    cursor: pointer;
}
#fp-nav ul li:hover .fp-tooltip,
#fp-nav.fp-show-active a.active + .fp-tooltip {
    -webkit-transition: opacity 0.2s ease-in;
    transition: opacity 0.2s ease-in;
    width: auto;
    opacity: 1;
}
#fp-nav ul li .fp-tooltip.right {
    right: 20px;
}
#fp-nav ul li .fp-tooltip.left {
    left: 20px;
}



//   

.fp-auto-height.fp-section,
.fp-auto-height .fp-slide,
.fp-auto-height .fp-tableCell{
    height: auto !important;
}

.fp-responsive .fp-auto-height-responsive.fp-section,
.fp-responsive .fp-auto-height-responsive .fp-slide,
.fp-responsive .fp-auto-height-responsive .fp-tableCell {
    height: auto !important;
}

#fp-nav.left {
  left: 50%;
  margin-top: 0 !important;
  transform: translate(-600px, -100%);

  @media (min-width: @screen-xl) {
    opacity: 0;

    .js-loaded & {
      animation: opacity-none 0.3s 1.2s forwards;
    }
  }

  @media (min-width: @screen-xxl) {
    transform: translate(-832px, -100%);
  }
}

#fp-nav ul {
  width: 68px;

  @media (min-width: @screen-xxl) {
    width: 106px;
  }

  li {
    display: block;
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;

    &:nth-child(2n) {
      padding-right: 8px;
    }

    a {
      position: relative;
      display: block;
      padding: 10px 0;
      width: 100%;
      height: 21px;

      span {
        position: absolute;
        display: block;
        top: 50%;
        right: 0;
        left: auto;
        height: 1px;
        width: 30px;
        border-radius: 0;
        margin: 0;
        background-color: @gray-light;
      }
    }
  }
}
#fp-nav ul li a.active span,
#fp-nav ul li:hover a.active span,
#fp-nav ul li:hover a span {
  width: 100%;
  height: 1px;
  margin: 0;
  border-radius: 0;
  background-color: @color-second;
}
