.article__tags {
  margin: 0;
  padding: 0;
  list-style: none;
  display: inline;
  font-family: 'Roboto Condensed', -apple-system, BlinkMacSystemFont, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
}
.article__tags li {
  display: inline;
  margin-right: 20px;
}
.article__tags a {
  white-space: nowrap;
  color: #999999;
  text-decoration: none;
  border-bottom: 1px dashed currentColor;
  transition: all 0.3s;
}
.article__tags a:hover,
.article__tags a:focus {
  color: #0c1028;
  text-decoration: none;
  border-color: transparent;
}
@media (min-width: 1200px) {
  .article--to-right {
    transform: translateX(100px);
  }
}
@media (min-width: 1920px) {
  .article--to-right {
    transform: translateX(257px);
  }
}
@media (max-width: 1199px) {
  .article--no-title {
    margin-bottom: 0;
  }
  .article--no-title .article__img-wrapper {
    left: auto;
    right: 0;
    top: -10px;
  }
}
@media (min-width: 1200px) {
  .article--no-title {
    padding-top: 25px;
    margin-bottom: 40px;
  }
}
@media (min-width: 1920px) {
  .article--no-title {
    margin-bottom: 70px;
  }
}
.connect-with-us {
  display: none;
  position: fixed;
  z-index: 10;
  right: 0;
  bottom: 0;
  left: 0;
}
@media (max-width: 1199px) {
  .connect-with-us {
    display: flex;
  }
}
.bg-name {
  position: fixed;
  z-index: 1;
  top: 70px;
  left: 0;
  width: 100%;
  overflow: hidden;
}
@media (min-width: 1200px) {
  .bg-name {
    top: 94px;
    left: calc(50% - 310px);
    opacity: 0;
  }
  .js-loaded .bg-name {
    animation: opacity-none 0.3s 0.9s forwards;
  }
}
@media (min-width: 1920px) {
  .bg-name {
    top: 174px;
    left: calc(50% - 460px);
  }
}
.bg-name:before {
  content: attr(data-bg-text);
  color: #262626;
  white-space: nowrap;
  font-size: 90px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1;
  letter-spacing: 0.08em;
  opacity: 0.05;
}
@media (min-width: 1200px) {
  .bg-name:before {
    font-size: 150px;
  }
}
@media (min-width: 1920px) {
  .bg-name:before {
    font-size: 178px;
  }
}
@media (min-width: 1200px) {
  .bg-name--left {
    top: 120px;
    left: calc(50% - 600px);
  }
}
@media (min-width: 1920px) {
  .bg-name--left {
    top: 219px;
    left: calc(50% - 960px);
  }
}
.page-404 {
  color: #d9d9d9;
  position: relative;
}
@media (max-width: 1199px) {
  .page-404 {
    padding: 20px;
    text-align: center;
    margin: auto;
  }
}
@media (min-width: 1200px) {
  .page-404 {
    width: 887px;
    margin: auto;
    opacity: 0;
  }
  .js-loaded .page-404 {
    animation: opacity-none 0.3s 1s forwards;
  }
}
@media (min-width: 1920px) {
  .page-404 {
    width: 1192px;
  }
}
.page-404__text {
  font-size: 18px;
}
@media (min-width: 1200px) {
  .page-404__text {
    max-width: 400px;
  }
}
.page-404__text p {
  margin-bottom: 30px;
}
@media (min-width: 1200px) {
  .page-404__name {
    margin: 0;
    padding: 0;
    font-weight: 700;
    line-height: 1;
    opacity: 0.2;
  }
}
@media (min-width: 1200px) {
  .page-404__4-1 {
    font-size: 360px;
    position: absolute;
    top: -25px;
    right: 50%;
    width: 160px;
    overflow: hidden;
    color: #95b0ff;
  }
}
@media (min-width: 1200px) {
  .page-404__0 {
    font-size: 360px;
    position: absolute;
    top: -25px;
    right: 25%;
    width: 160px;
    overflow: hidden;
    color: #95b0ff;
  }
}
@media (min-width: 1200px) {
  .page-404__4-2 {
    font-size: 360px;
    position: absolute;
    top: -25px;
    right: 0;
    width: 160px;
    overflow: hidden;
    color: #95b0ff;
  }
}
