@import '../../less/variables.less';     //      


.discuss-project-btn {
  position: absolute;
  z-index: 6;
  top: 37px;
  left: e('calc(50% + 444px)');
  font-size: 16px;
  min-width: 100px;
  padding: 0 14px 0 9px;

  .main-nav--home & {
    transform: translateX(50vw);
  }

  .main-nav--content & {
    color: @gray-darkest;

    &:hover,
    &:focus {
      color: #fff;
    }
  }

  .js-loaded .main-nav--home & {
    animation: translate-none 0.2s 0.8s forwards;
  }

  @media (max-width: (@screen-xl - 1)) {
    display: none;
  }

  @media (min-width: @screen-xxl) {
    font-size: 18px;
    left: e('calc(50% + 596px)');
    padding-left: 21px;
    padding-right: 37px;
  }
}
